 #inp__t {
     padding-top: 30px;
     
 }
 #im__g {
     margin-top: 60px;
     object-fit: contain;
     height: 90vh
 }
 #im__g1 {
     height: 4vh;
     object-fit: contain;
 }
 #header__2{
     padding-top: 10px;
 }

#ip {
    border-radius: 10px;
    border-color:  #3498db ;   
    border-width: 3px;
    padding: 30px;    
}

#bt__n {
    border-radius: 10px;
    border-color: gray;
    background-color: #3498db;
    color: white;
    border-width: 3px;
    margin-left: 10px;
}

.header-img{
    text-align: right;
    height: 50vh;
    
}

#header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}


@-webkit-keyframes up-down {
    0% {
        transform: translate(15px);
    }
    100%{
        transform: translate(-15px);
    }
}

@keyframes up-down {
    0% {
        transform: translate(10px);
    }
    100%{
        transform: translate(-10px);
    }
}


