@media (max-width: 360px) {

    #header {
        /* margin-top: 20px;
        height: 60vh;
         width: 100%;  */
    }
    #header h1 {
        font-size: 25px;
        
    }
    #header h2 {
        font-size: 20px;
        
    }
    .header-img{
        text-align: center;
        height: 35vh;
        
        
    }    
}

@media (max-width: 540px) {
    #header h1 {
        font-size: 30px;
        
    }
    #header h2 {
        font-size: 25px;
    }
    .header-img {
        height: 35vh;
    }
}

@media (max-width: 770px) {
    #header {
        width: 130%;
        margin-top: -80px;
    }

    #header h1 {
        font-size: 40px;
        
        
        
    }
    #header h2 {
        font-size: 25px;
    }
    .header-img {
        height: 30vh;
        width: 25vh;
        margin-top: -140px;
        
    }
}






