.navBar {
    
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    
    
    
} 
 
 .navBar__icon {
    object-fit: contain;
    height: 30px;
    margin-left: 80px;
    margin-top: 10px;
    margin-bottom: 10px;
     margin-right: 20px; 
}  

#btn1 {
    border-color: gray;
    border-width: 1.5px;
    margin-top: 10px;
    border-radius: 20px;
    
}

#btn2 {
    margin-right: 50px;
    border-radius: 20px;
    margin-top: 10px;
    border-width: 1px;
}

.navBar a:hover{
    color: #3498db !important;
}


@media (max-width: 360px) {
 .navBar__icon {
     margin-left: inherit;
 }
 
 #btn1 {
     margin-left: 30px !important;
     
 }
 #btn2 {
     margin-left: 30px !important;
 }

 .nav-link {
     margin-left: 30px !important;
 }
}


@media (max-width: 540px) {
    .navBar__icon {
        margin-left: inherit;
    }
    .nav-link {
        margin-left: 35px !important;
    }
    #btn1 {
        margin-left: 35px !important;
    }
    #btn2 {
        margin-left: 35px !important;
    }
}

@media (max-width: 770px) {
    .navBar__icon {
        margin-left: inherit;
        height: 40px;
    }
    .nav-link {
        margin-left: 40px !important;
        font-size: 25px;
    }
    #btn1 {
        margin-left: 40px !important;
        font-size: 20px;
    }
    #btn2 {
        margin-left: 40px !important;
        font-size: 20px;
    }
}