/* Backed by the best  */

.header-extradiv{
	width: 100%;
	height: auto;
	margin: 100px 0;
	text-align: center;
}

#i_1 {
    margin-top: 20px;
    width: 30vh;
}
#i_2 {
    
    width: 30vh;
}
#i_3 {
    height: 10vh;
    width: 30vh;
}

.extra-div{
	background: #fff;
	border: medium none;
	padding: 50px!important;
	border-radius: 2px;
    transition: 0.3s;
    margin-top: 10px;
}

.extra-div:hover{
	box-shadow: 0 0 20px 0 rgba(0,0,0,0.3);
    transform: translateY(-20px);
    /* background:  #3498db; */
}

