/* Responsiveness */

@media (max-width: 360px) {
    .Mid__sec{
        margin-top: 520px;
    }   
    .head {
        font-size: 25px;
        text-transform: inherit;
    }
    #saving h2 {
        font-size: 17px;
        margin-top: -50px;
    }
    #saving h1{
        margin-top: 15px;
        font-size: 25px;
        
    }
    #saving h3 {
        font-size: 17px;
    }
    .sign__up{
        font-size: 30px;
    }
    .sign__d {
        width: 100%;
        margin-left: inherit;
        font-size: 17px;
    }
}

@media (max-width: 540px) {
    .Mid__sec{
        margin-top: 620px;
    }   
    .head {
        font-size: 30px;
